<script>
import { mapGetters } from "vuex";

export default {
  name: "addDataClient",
  props: ["show", "day", "month", "year"],
  data() {
    return {
      loading: false,
      clientData: {
        macros: {
          proteins: Number,
          carbs: Number,
          fats: Number
        },
        steps: {
          number: Number,
          distance: Number,
          period: Number
        },
        weight: {
          weight: Number,
          fat: Number,
          muscle: Number,
          bone: Number,
          bmi: Number,
          notes: ""
        },
        comment: ""
      }
    };
  },
  computed: mapGetters([
    "types",
    "values",
    "activeClient",
    "macros",
    "user",
    "getDayData"
  ]),
  methods: {
    close() {
      this.client = {};

      this.$emit("closed", this.show);
    },
    saveData() {
      this.$store.dispatch("uploadDayClient", this.clientData);
    },
    addWarmUp() {
      this.warmUpExercises.push({
        name: "",
        value: "",
        option: ""
      });
    },
    addWorkout() {
      this.workOutExercises.push({
        name: "",
        value: "",
        option: ""
      });
    },
    addCoolUp() {
      this.coolUpExercises.push({
        name: "",
        value: "",
        option: ""
      });
    },
    deleteWarmUp(index) {
      this.warmUpExercises.splice(index, 1);
    },
    deleteWorkout(index) {
      this.workOutExercises.splice(index, 1);
    },
    deleteCoolUp(index) {
      this.coolUpExercises.splice(index, 1);
    }
  }
};
</script>

<style scoped>
