<template>
  <v-content>
    <v-overlay :value="loading">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
    <addData :show="openAddData" :day="day" :month="month" :year="year" @closed="closeAddData"></addData>
    <viewData :show="openViewData" :day="day" :month="month" :year="year" @closed="closeViewData"></viewData>
    <addDataClient
      :show="openAddDataClient"
      :day="day"
      :month="month"
      :year="year"
      @closed="closeAddDataClient"
    ></addDataClient>
    <v-sheet height="50">
      <v-row align="center" justify="space-between">
        <v-col cols="auto">
          <v-btn fab text small color="grey darken-2" @click="prev">
            <v-icon small>mdi-chevron-left</v-icon>
          </v-btn>
        </v-col>
        <v-col cols="auto">
          <h3>{{ title }}</h3>
        </v-col>
        <v-col cols="auto">
          <v-btn fab text small color="grey darken-2" @click="next">
            <v-icon small>mdi-chevron-right</v-icon>
          </v-btn>
        </v-col>
      </v-row>
    </v-sheet>
    <v-sheet height="815" width="100%">
      <v-calendar
        ref="calendar"
        v-model="focus"
        :value="today"
        color="primary"
        :event-color="getEventColor"
        :weekdays="[1, 2, 3, 4, 5, 6, 0]"
        :event-more="eventMore"
        @change="updateRange"
      >
        <template v-slot:day="{ present, future, day, month, year,weekday }">
          <div v-if="month === currentMonth">
            <v-row align="center" justify="center" no-gutters>
              <div class="square center exercises">
                <span>&#10004;</span>
              </div>
              <div class="square center macros">
                <p>&#10004;</p>
              </div>
              <div class="square center steps">
                <p>&#10004;</p>
              </div>
            </v-row>
            <v-row align="center" justify="center" no-gutters>
              <div class="square center weight">
                <p>&#10004;</p>
              </div>
              <div class="square center payment">
                <p>&#10004;</p>
              </div>
              <div class="square center comments">
                <p>&#10004;</p>
              </div>
            </v-row>
            <v-row style="padding-top:15px" align="center" justify="center" no-gutters>
              <div v-if="future === false && present === false">
                <v-btn
                  style="background-color:grey; border-radius:40px"
                  @click="viewData(day, month, year ,present,future)"
                  :small="true"
                >
                  <v-icon>visibility</v-icon>
                </v-btn>
              </div>
              <div v-else-if="future === true || present === true && user.role === 3002">
                <v-btn
                  style="background-color:grey; border-radius:40px"
                  @click="addDataClient(day, month, year ,weekday)"
                  :small="true"
                >
                  <v-icon>add</v-icon>
                </v-btn>
              </div>
              <div
                v-else-if="future === true || present === true && (user.role ===3000 || user.role === 3001)"
              >
                <v-btn
                  style="background-color:grey; border-radius:40px"
                  @click="addDateData(day, month, year ,present,future)"
                  :small="true"
                >
                  <v-icon>add</v-icon>
                </v-btn>
              </div>

              <div v-else>
                <v-btn
                  style="background-color:grey; border-radius:40px"
                  @click="viewData(day, month, year ,present,future)"
                  :small="true"
                >
                  <v-icon>add</v-icon>
                </v-btn>
              </div>
            </v-row>
          </div>
        </template>
      </v-calendar>
    </v-sheet>
  </v-content>
</template>

<script>
import { mapGetters } from "vuex";
import addData from "../components/addData";
import viewData from "../components/viewData";
import addDataClient from "../components/addDataClient";
import moment from "moment";

export default {
  name: "Calendar",
  components: { addData, viewData, addDataClient },
  data() {
    return {
      focus: "",
      type: "month",
      currentMonth: new Date().getMonth() + 1,
      day: "",
      month: "",
      year: "",
      openAddData: false,
      openViewData: false,
      openAddDataClient: false,
      loading: false,
      eventMore: false,

      start: null,
      end: null,
      startOfCalendar: null,
      endOfCalendar: null,
      selectedEvent: {},
      selectedElement: null,
      selectedOpen: false,
      today: "2020-05-01",
      events: [],
      colors: [
        "blue",
        "indigo",
        "deep-purple",
        "cyan",
        "green",
        "orange",
        "grey darken-1"
      ],
      names: [
        "Meeting",
        "Holiday",
        "PTO",
        "Travel",
        "Event",
        "Birthday",
        "Conference",
        "Party"
      ]
    };
  },
  created() {
    this.startOfCalendar = moment()
      .startOf("month")
      .format("YYYY-MM-DD");
    this.endOfCalendar = moment()
      .endOf("month")
      .format("YYYY-MM-DD");
    console.log(this.startOfCalendar);
  },
  computed: {
    ...mapGetters(["user", "activeClient", "getDayData"]),
    title() {
      const { start, end } = this;
      if (!start || !end) {
        return "";
      }

      const startMonth = this.monthFormatter(start);
      const endMonth = this.monthFormatter(end);
      const suffixMonth = startMonth === endMonth ? "" : endMonth;

      const startYear = start.year;
      const endYear = end.year;
      const suffixYear = startYear === endYear ? "" : endYear;

      const startDay = start.day + this.nth(start.day);
      const endDay = end.day + this.nth(end.day);

      switch (this.type) {
        case "month":
          return `${startMonth} ${startYear}`;
        case "week":
        case "4day":
          return `${startMonth} ${startDay} ${startYear} - ${suffixMonth} ${endDay} ${suffixYear}`;
        case "day":
          return `${startMonth} ${startDay} ${startYear}`;
      }
      return "";
    },
    monthFormatter() {
      return this.$refs.calendar.getFormatter({
        timeZone: "UTC",
        month: "long"
      });
    }
  },
  mounted() {
    this.$refs.calendar.checkChange();
  },
  methods: {
    closeAddData() {
      this.openAddData = false;
    },
    closeViewData() {
      this.openViewData = false;
    },
    closeAddDataClient() {
      this.openAddDataClient = false;
    },
    async addDataClient(day, month, year, weekday) {
      this.loading = true;
      this.day = day;
      this.month = month;
      this.year = year;

      let date = new Date(
        `${this.year}-${this.month}-${this.day}`
      ).toISOString();

      date = moment(date).format("YYYY-MM-DD");

      await this.$store.dispatch("getExercisesClient", date);
      await this.$store.dispatch("getMacrosClient", weekday);

      if (this.getDayData.date) {
        this.loading = false;
        this.openAddDataClient = true;
      } else {
        console.log("You have no exercises");
        this.loading = false;
      }
    },
    async viewData(day, month, year) {
      this.loading = true;

      this.day = day;
      this.month = month;
      this.year = year;

      const date = new Date(
        `${this.year}-${this.month}-${this.day}`
      ).toISOString();
      const data = {
        user: this.activeClient.user,
        date: moment(date).format("YYYY-MM-DD")
      };

      this.openViewData = true;
      await this.$store.dispatch("getExercises", data);
      await this.$store.dispatch("getMacros", this.activeClient.user);
      this.loading = false;
    },
    async addDateData(day, month, year) {
      this.loading = true;

      this.day = day;
      this.month = month;
      this.year = year;

      const date = new Date(
        `${this.year}-${this.month}-${this.day}`
      ).toISOString();
      const data = {
        user: this.activeClient.user,
        date: moment(date).format("YYYY-MM-DD")
      };

      this.openAddData = true;
      await this.$store.dispatch("getExercises", data);
      await this.$store.dispatch("getMacros", this.activeClient.user);
      this.loading = false;
    },
    showData(day) {
      console.log(day);
    },
    viewDay(data) {
      console.log(data);
    },
    getEventColor(event) {
      return event.color;
    },
    setToday() {
      this.focus = this.today;
    },
    prev() {
      this.$refs.calendar.prev();
    },
    next() {
      this.$refs.calendar.next();
    },
    showEvent({ nativeEvent, event }) {
      const open = () => {
        this.selectedEvent = event;
        this.selectedElement = nativeEvent.target;
        setTimeout(() => (this.selectedOpen = true), 10);
      };

      if (this.selectedOpen) {
        this.selectedOpen = false;
        setTimeout(open, 10);
      } else {
        open();
      }

      nativeEvent.stopPropagation();
    },
    updateRange({ start, end }) {
      const events = [];

      //   const min = new Date(`${start.date}T00:00:00`);
      //   const max = new Date(`${end.date}T23:59:59`);
      //   const days = Math.round((max.getTime() - min.getTime()) / 86400000);
      //   const eventCount = this.rnd(days, days + 20);

      // console.log(start);
      // console.log(end);
      for (let i = 1; i <= end.day; i++) {
        // const allDay = this.rnd(0, 3) === 0;
        // const firstTimestamp = this.rnd(min.getTime(), max.getTime());
        // const first = new Date(firstTimestamp - (firstTimestamp % 900000));
        // const secondTimestamp = this.rnd(2, allDay ? 288 : 8) * 900000;
        // const second = new Date(first.getTime() + secondTimestamp);

        // events.push({
        //   name: this.names[this.rnd(0, this.names.length - 1)],
        //   start: this.formatDate(first, !allDay),
        //   end: this.formatDate(second, !allDay),
        //   color: this.colors[this.rnd(0, this.colors.length - 1)]
        // });
        events.push(
          {
            start: `2020-${start.month}-${i}`,
            end: `2020-${start.month}-${i}`,
            color: "cyan",
            name: "✔"
          },
          {
            start: `2020-${start.month}-${i}`,
            end: `2020-${start.month}-${i}`,
            color: "red",
            name: "✔"
          },
          {
            start: `2020-${start.month}-${i}`,
            end: `2020-${start.month}-${i}`,
            color: "orange",
            name: "✔"
          },
          {
            start: `2020-${start.month}-${i}`,
            end: `2020-${start.month}-${i}`,
            color: "green",
            name: "✔"
          },
          {
            start: `2020-${start.month}-${i}`,
            end: `2020-${start.month}-${i}`,
            color: "yellow",
            name: "✔"
          },
          {
            start: `2020-${start.month}-${i}`,
            end: `2020-${start.month}-${i}`,
            color: "blue",
            name: "✔"
          }
        );
      }
      this.start = start;
      this.end = end;
      this.events = events;
    },
    nth(d) {
      return d > 3 && d < 21
        ? "th"
        : ["th", "st", "nd", "rd", "th", "th", "th", "th", "th", "th"][d % 10];
    },
    rnd(a, b) {
      return Math.floor((b - a + 1) * Math.random()) + a;
    },
    formatDate(a, withTime) {
      return withTime
        ? `${a.getFullYear()}-${a.getMonth() +
            1}-${a.getDate()} ${a.getHours()}:${a.getMinutes()}`
        : `${a.getFullYear()}-${a.getMonth() + 1}-${a.getDate()}`;
    }
  }
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}

.center {
  text-align: center;
  padding: 0px 0;
}
.square {
  height: 23px;
  width: 23px;
  border-radius: 7px;
}

.exercises {
  background-color: cyan;
}

.macros {
  background-color: purple;
}

.steps {
  background-color: orange;
}

.weight {
  background-color: green;
}

.payment {
  background-color: yellow;
}

.comments {
  background-color: blue;
}
</style>
