<template>
  <v-content>
    <v-dialog v-model="show" width="700" @click:outside="close">
      <v-overlay :value="loading">
        <v-progress-circular indeterminate size="64"></v-progress-circular>
      </v-overlay>
      <v-sheet>
        <v-tabs style="background-color: blue !important;" grow slider-color="black">
          <v-tab class="exercises" v-ripple="false">Exercises</v-tab>
          <v-tab class="macros" v-ripple="false">Macros</v-tab>
          <v-tab class="steps" v-ripple="false">Steps</v-tab>
          <v-tab class="weight" v-ripple="false">Weight</v-tab>
          <v-tab class="payment" v-ripple="false">Payment</v-tab>
          <v-tab class="comments" v-ripple="false">Comments</v-tab>

          <v-tab-item>Exercises</v-tab-item>
          <v-tab-item>Macros</v-tab-item>
          <v-tab-item>Steps</v-tab-item>
          <v-tab-item>Weight</v-tab-item>
          <v-tab-item>Payment</v-tab-item>
          <v-tab-item>Comments</v-tab-item>
        </v-tabs>
      </v-sheet>
    </v-dialog>
  </v-content>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "viewData",
  props: ["show", "day", "month", "year"],
  data() {
    return {
      loading: false
    };
  },
  computed: mapGetters([
    "types",
    "values",
    "activeClient",
    "macros",
    "getDayData"
  ]),
  methods: {
    close: function() {
      //   this.client = {};
      //   this.$refs.form.resetValidation();
      this.$emit("closed", this.show);
    },
    addWarmUp: function() {
      this.warmUpExercises.push({
        name: "",
        value: "",
        option: ""
      });
    },
    addWorkout: function() {
      this.workOutExercises.push({
        name: "",
        value: "",
        option: ""
      });
    },
    addCoolUp: function() {
      this.coolUpExercises.push({
        name: "",
        value: "",
        option: ""
      });
    },
    deleteWarmUp: function(index) {
      this.warmUpExercises.splice(index, 1);
    },
    deleteWorkout: function(index) {
      this.workOutExercises.splice(index, 1);
    },
    deleteCoolUp: function(index) {
      this.coolUpExercises.splice(index, 1);
    }
  }
};
</script>

<style scoped>
.exercises {
  background-color: #4dd7fa;
  color: black;
  border-radius: 20px 20px 0 0 !important;
}
.macros {
  background-color: #e93578;
  color: black;
  border-radius: 20px 20px 0 0 !important;
}

.steps {
  background-color: #ffa834;
  color: black;
  border-radius: 20px 20px 0 0 !important;
}

.weight {
  background-color: #629c44;
  color: black;
  border-radius: 20px 20px 0 0 !important;
}

.payment {
  background-color: #f4eb49;
  color: black;
  border-radius: 20px 20px 0 0 !important;
}

.comments {
  background-color: #006ce5;
  color: black;
  border-radius: 20px 20px 0 0 !important;
}

.center {
  text-align: center;
}
