<template>
  <v-content class="container">
    <div class="chart-container">
      <canvas height="150" class="chart" id="firstChart"></canvas>
    </div>
    <div class="chart-container">
      <canvas height="150" class="chart" id="secondChart"></canvas>
    </div>

    <div class="chart-container">
      <canvas height="150" class="chart" id="thirdChart"></canvas>
    </div>
  </v-content>
</template>

<script>
import Chart from "chart.js";

export default {
  name: "Charts",
  data() {
    return {};
  },
  mounted() {
    this.createCharts();
  },
  methods: {
    createCharts: function() {
      let firstChart = document.getElementById("firstChart").getContext("2d");
      let secondChart = document.getElementById("secondChart").getContext("2d");
      let thirdChart = document.getElementById("thirdChart").getContext("2d");

      new Chart(firstChart, {
        type: "line",

        data: {
          labels: ["day1", "day2", "day3", "day4", "day5", "day6", "day7"],
          datasets: [
            {
              label: "Protein",
              data: [10, 30, 20, 50, 35, 20, 40],
              borderColor: "green",
              backgroundColor: "green",
              fill: false,
              maintainAspectRatio: false
            },
            {
              label: "Carbs",
              data: [10, 10, 30, 50, 35, 40, 20],
              borderColor: "blue",
              backgroundColor: "blue",
              fill: false,
              maintainAspectRatio: false
            },
            {
              label: "Fat",
              data: [10, 15, 50, 20, 55, 20, 20],
              borderColor: "purple",
              backgroundColor: "purple",
              fill: false,
              maintainAspectRatio: false
            }
          ]
        },
        options: { responsive: true }
      });

      new Chart(secondChart, {
        type: "line",

        data: {
          labels: ["week1", "week2", "week3", "week4"],
          datasets: [
            {
              label: "Weight",
              data: [10, 30, 20, 50],
              borderColor: "green",
              backgroundColor: "green",
              fill: false,
              maintainAspectRatio: false
            }
          ]
        },
        options: { responsive: true }
      });

      new Chart(thirdChart, {
        type: "line",

        data: {
          labels: ["day1", "day2", "day3", "day4", "day5", "day6", "day7"],
          datasets: [
            {
              label: "Protein",
              data: [10, 30, 20, 50, 35, 20, 40],
              borderColor: "green",
              backgroundColor: "green",
              fill: false,
              maintainAspectRatio: false
            }
          ]
        },
        options: { responsive: true }
      });
    }
  }
};
</script>

<style >
.chart-container {
  position: relative;
  margin: auto;
}
.chart {
  height: 21.5vh;
}
.container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
</style>