                        item-value="id"
                        :full-width="true"
                        filled
                        dense
                        rounded
                        class="fieldStyle"
                      ></v-select>
                    </v-col>
                    <v-divider vertical></v-divider>
                    <v-col cols="2">
                      <v-btn @click="deleteWarmUp(index)" block text>
                        <v-icon>clear</v-icon>
                      </v-btn>
                    </v-col>
                  </v-row>
                  <v-divider></v-divider>
                </div>
                <v-row style="margin:0">
                  <v-btn block text @click="addWarmUp">Add exercise</v-btn>
                </v-row>
                <v-divider></v-divider>
                <v-row
                  style="background-color: rgb(9, 255, 0);padding:5px 0 5px 0;margin:0"
                  justify="center"
                >
                  <h3>Workout</h3>
                </v-row>
                <v-divider></v-divider>
                <v-row style="margin:0" align="center" justify="center">
                  <v-col class="center">Name</v-col>
                  <v-divider vertical></v-divider>
                  <v-col class="center">Value</v-col>
                  <v-divider vertical></v-divider>
                  <v-col class="center">Kg/Min/Rep/Sec</v-col>
                  <v-divider vertical></v-divider>
                  <v-col cols="2" class="center">Delete</v-col>
                </v-row>
                <v-divider></v-divider>
                <div v-for="(exercise, index) in getDayData.workoutExercises" :key="'A'+index">
                  <v-row style="margin:0" align="center" justify="center">
                    <v-col class="center">
                      <v-select
                        v-model="exercise.name"
                        :items="values.workoutExercises"
                        item-text="name"
                        item-value="id"
                        :full-width="true"
                        filled
                        dense
                        rounded
                        class="fieldStyle"
                      ></v-select>
                    </v-col>
                    <v-divider vertical></v-divider>
                    <v-col>
                      <v-text-field
                        v-model.number="exercise.value"
                        type="number"
                        min="0"
                        :full-width="true"
                        filled
                        dense
                        rounded
                        class="fieldStyle"
                      ></v-text-field>
                    </v-col>
                    <v-divider vertical></v-divider>
                    <v-col>
                      <v-select
                        v-model="exercise.option"
                        :items="values.exerciseOptions"
                        item-text="name"
                        item-value="id"
                        :full-width="true"
                        filled
                        dense
                        rounded
                        class="fieldStyle"
                      ></v-select>
                    </v-col>
                    <v-divider vertical></v-divider>
                    <v-col cols="2">
                      <v-btn @click="deleteWorkout(index)" block text>
                        <v-icon>clear</v-icon>
                      </v-btn>
                    </v-col>
                  </v-row>
                  <v-divider></v-divider>
                </div>
                <v-row style="margin:0">
                  <v-btn block text @click="addWorkout">Add exercise</v-btn>
                </v-row>
                <v-divider></v-divider>
                <v-row
                  style="background-color: rgb(9, 255, 0);padding:5px 0 5px 0;margin:0"
                  justify="center"
                >
                  <h3>Cool up</h3>
                </v-row>
                <v-divider></v-divider>
                <v-row style="margin:0" align="center" justify="center">
                  <v-col class="center">Name</v-col>
                  <v-divider vertical></v-divider>
                  <v-col class="center">Value</v-col>
                  <v-divider vertical></v-divider>
                  <v-col class="center">Kg/Min/Rep/Sec</v-col>
                  <v-divider vertical></v-divider>
                  <v-col cols="2" class="center">Delete</v-col>
                </v-row>
                <v-divider></v-divider>
                <div v-for="(exercise, index) in getDayData.coolUpExercises" :key="'B' + index">
                  <v-row style="margin:0" align="center" justify="center">
                    <v-col class="center">
                      <v-select
                        v-model="exercise.name"
                        :items="values.coolUpExercises"
                        item-text="name"
                        item-value="id"
                        :full-width="true"
