                </v-row>
              </v-form>
            </div>
          </v-tab-item>
          <v-tab-item>
            <v-divider></v-divider>
            <div>
              <v-row style="margin:0" align="center" justify="center">
                <v-col class="center">Day</v-col>
                <v-divider vertical></v-divider>
                <v-col class="center">Proteins</v-col>
                <v-divider vertical></v-divider>
                <v-col class="center">Carbs</v-col>
                <v-divider vertical></v-divider>
                <v-col class="center">Fats</v-col>
                <v-divider vertical></v-divider>
                <v-col class="center">Calories</v-col>
                <v-divider vertical></v-divider>
                <v-col class="center">Training Day</v-col>
              </v-row>
              <v-divider></v-divider>
              <div v-for="(macro, index) in Object.keys(macros)" :key="index">
                <v-row align="center" justify="center" style="margin:0">
                  <v-divider vertical></v-divider>
                  <v-col class="center">{{ macro }}</v-col>
                  <v-divider vertical></v-divider>
                  <v-col>
                    <v-text-field
                      v-model="macros[macro].proteins"
                      type="number"
                      min="0"
                      :full-width="true"
                      filled
                      dense
                      rounded
                      class="fieldStyle"
                    ></v-text-field>
                  </v-col>
                  <v-divider vertical></v-divider>
                  <v-col>
                    <v-text-field
                      v-model="macros[macro].carbs"
                      type="number"
                      min="0"
                      :full-width="true"
                      filled
                      dense
                      rounded
                      class="fieldStyle"
                    ></v-text-field>
                  </v-col>
                  <v-divider vertical></v-divider>
                  <v-col>
                    <v-text-field
                      v-model="macros[macro].fats"
                      type="number"
                      min="0"
                      :full-width="true"
                      filled
                      dense
                      rounded
                      class="fieldStyle"
                    ></v-text-field>
                  </v-col>
                  <v-divider vertical></v-divider>
                  <v-col>
                    <v-text-field
                      v-model="macros[macro].calories"
                      type="number"
                      min="0"
                      :full-width="true"
                      filled
                      dense
                      rounded
                      class="fieldStyle"
                    ></v-text-field>
                  </v-col>
                  <v-divider vertical></v-divider>
                  <v-col>
                    <v-checkbox style="padding:0 0 25px 35%;height:5px;margin:0" disabled></v-checkbox>
                  </v-col>
                </v-row>
              </div>
              <v-divider></v-divider>
              <v-row style="margin:0" align="center" justify="center">
                <h3>If you dont add to a day, the previous value will be saved</h3>
              </v-row>
            </div>
          </v-tab-item>
        </v-tabs>
        <v-divider></v-divider>
        <v-row style="padding:20px 3% 20px 3%" align="center" justify="space-between" no-gutters>
          <v-btn @click="close" color="error" :normal="true">Cancel</v-btn>
          <v-btn color="primary" @click="saveDay" :normal="true">Save</v-btn>
        </v-row>
      </v-sheet>
    </v-dialog>
  </v-content>
</template>

<script>
import { mapGetters } from "vuex";
import moment from "moment";

export default {
  name: "addData",
  props: ["show", "day", "month", "year"],
  data() {
    return {
      loading: false
    };
  },
  computed: mapGetters([
    "types",
    "values",
    "activeClient",
    "macros",
    "getDayData"
  ]),
  methods: {
    async saveDay() {
      this.loading = true;
      const date = new Date(
        `${this.year}-${this.month}-${this.day}`
      ).toISOString();

      this.getDayData.date = moment(date).format("YYYY-MM-DD");
      this.getDayData.user = this.activeClient.user;
      this.getDayData.macros = { ...this.macros };
      console.log(this.getDayData);
      await this.$store.dispatch("uploadDayData", this.getDayData);
      this.loading = false;
    },
    close() {
      // this.client = {};
      // this.$refs.form.resetValidation();
      this.$emit("closed", this.show);
    },
    addWarmUp() {
      this.getDayData.warmUpExercises.push({
        name: "",
        value: "",
        option: ""
      });
    },
    addWorkout() {
      this.getDayData.workoutExercises.push({
        name: "",
        value: "",
        option: ""
      });
    },
    addCoolUp() {
      this.getDayData.coolUpExercises.push({
        name: "",
        value: "",
        option: ""
      });
    },
    deleteWarmUp(index) {
      this.getDayData.warmUpExercises.splice(index, 1);
    },
    deleteWorkout(index) {
      this.getDayData.workoutExercises.splice(index, 1);
    },
    deleteCoolUp(index) {
      this.getDayData.coolUpExercises.splice(index, 1);
    }
  }
};
</script>
<style scoped>
.exercises {
  background-color: #81d452;
  color: black;
  border-radius: 30 !important;
}
.macros {
  background-color: #00c8f8;
}

.center {
  text-align: center;
}
.fieldStyle {
  height: 44px;
  margin-top: 5px;
}
</style>